<template>
  <div class="hot" ref="hot"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.setupEcharts();
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.hot;
      if (chartDom) {
        const myChart = this.echarts.init(chartDom);
        const option = {
          title: {
            top: 30,
            left: 'center',
            text: '每日提交量统计热力图',
          },
          tooltip: {},
          visualMap: {
            min: 0,
            max: 10000,
            type: 'piecewise',
            orient: 'horizontal',
            left: 'center',
            top: 65,
          },
          calendar: {
            top: 120,
            left: 30,
            right: 30,
            cellSize: ['auto', 13],
            range: '2021',
            itemStyle: {
              borderWidth: 0.5,
            },
            yearLabel: { show: false },
          },
          series: {
            type: 'heatmap',
            coordinateSystem: 'calendar',
            data: this.getVirtulData('2021'),
          },
        };
        myChart.setOption(option);
      }
    },
    getVirtulData(year) {
      const date = +this.echarts.number.parseDate(`${year}-01-01`);
      const end = +this.echarts.number.parseDate(`${+year + 1}-01-01`);
      const dayTime = 3600 * 24 * 1000;
      const data = [];
      for (let time = date; time < end; time += dayTime) {
        data.push([
          this.echarts.format.formatTime('yyyy-MM-dd', time),
          Math.floor(Math.random() * 10000),
        ]);
      }
      return data;
    },
  },
};
</script>

<style>
</style>



