<template>
  <div class="pressure" ref="pressure"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.setupEcharts();
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.pressure;
      if (chartDom) {
        const myChart = this.echarts.init(chartDom);
        const option = {
          title: {
            bottom: 20,
            left: 'center',
            text: '台架使用率',
          },
          tooltip: {
            formatter: '{a} <br/>{b} : {c}%',
          },
          series: [
            {
              name: 'Pressure',
              type: 'gauge',
              progress: {
                show: true,
              },
              detail: {
                valueAnimation: true,
                formatter: '{value}',
              },
              data: [
                {
                  value: 36,
                  name: '使用率',
                },
              ],
            },
          ],
        };
        myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



