<template>
  <div class="testbar" ref="testbar"></div>
</template>

<script>
export default {
  data() {
    return {
      name: [],
      count: [],
      time: {},
    };
  },
  props: {},
  created() {
    this.Axios.get('/dashboard').then((res) => {
      this.$bus.$emit('render', res.data);
    });
  },
  mounted() {
    this.$bus.$on('render', (data) => {
      Object.keys(data).forEach((item) => {
        this.time[item] = data[item];
      });
      this.setupEcharts();
      console.log(this.time);
    });
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.testbar;
      if (chartDom) {
        const myChart = this.echarts.init(chartDom);
        const option = {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // Use axis to trigger tooltip
              type: 'shadow', // 'shadow' as default; can also be 'line' or 'shadow'
            },
          },
          legend: {},
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true,
          },
          xAxis: {
            type: 'value',
          },
          yAxis: {
            type: 'category',
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'],
          },
          series: [
            {
              name: 'Direct',
              type: 'bar',
              stack: 'total',
              label: {
                show: true,
              },
              emphasis: {
                focus: 'series',
              },
              data: this.time.time1,
            },
            {
              name: 'Mail Ad',
              type: 'bar',
              stack: 'total',
              label: {
                show: true,
              },
              emphasis: {
                focus: 'series',
              },
              data: this.time.time2,
            },
            {
              name: 'Affiliate Ad',
              type: 'bar',
              stack: 'total',
              label: {
                show: true,
              },
              emphasis: {
                focus: 'series',
              },
              data: this.time.time3,
            },
            {
              name: 'Video Ad',
              type: 'bar',
              stack: 'total',
              label: {
                show: true,
              },
              emphasis: {
                focus: 'series',
              },
              data: this.time.time4,
            },
            {
              name: 'Search Engine',
              type: 'bar',
              stack: 'total',
              label: {
                show: true,
              },
              emphasis: {
                focus: 'series',
              },
              data: this.time.time5,
            },
          ],
        };
        myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



