<template>
  <div class="testline" ref="testline"></div>
</template>

<script>
export default {
  data() {
    return {
      name: [],
      count: [],
      myChart: null,
    };
  },
  props: {},
  async created() {
    await this.Axios.get('/getBarData').then((result) => {
      const keys = Object.keys(result.data);
      keys.forEach((item) => {
        this.name.push(result.data[item].name);
        this.count.push(result.data[item].num);
      });
      this.$bus.$emit('flag');
    });
    console.log(this.name, this.count);
  },
  mounted() {
    this.$bus.$on('flag', () => {
      this.setupEcharts();
    });
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.testline;
      if (chartDom) {
        this.myChart = this.echarts.init(chartDom);
        this.myChart.off('click').on('click', () => {
          this.Axios.get('/dashboard').then((res) => {
            console.log(res);
            this.$bus.$emit('render', res.data);
          });
        });
        const option = {
          xAxis: {
            type: 'category',
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          },
          yAxis: {
            type: 'value',
          },
          series: [
            {
              data: [820, 932, 901, 934, 1290, 1330, 1320],
              type: 'line',
              smooth: true,
            },
          ],
        };
        this.myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



