<template>
  <div class="bar" ref="bar"></div>
</template>

<script>
export default {
  data() {
    return {
      name: [],
      count: [],
    };
  },
  props: {},
  async created() {
    await this.Axios.get('/getBarData').then((result) => {
      const keys = Object.keys(result.data);
      keys.forEach((item) => {
        this.name.push(result.data[item].name);
        this.count.push(result.data[item].num);
      });
      this.$bus.$emit('flag');
    });
    console.log(this.name, this.count);
  },
  mounted() {
    this.$bus.$on('flag', () => {
      this.setupEcharts();
    });
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.bar;
      if (chartDom) {
        const myChart = this.echarts.init(chartDom);
        const option = {
          title: {
            text: '脚本使用排行',
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow',
            },
          },
          legend: {},
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true,
          },
          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01],
          },
          yAxis: {
            type: 'category',
            data: this.name,
          },
          series: [
            {
              name: '脚本使用次数',
              type: 'bar',
              data: this.count,
            },
          ],
        };
        myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



