<template>
  <div class="pie" ref="pie"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  mounted() {
    this.setupEcharts();
  },
  methods: {
    setupEcharts() {
      const chartDom = this.$refs.pie;
      if (chartDom) {
        const myChart = this.echarts.init(chartDom);
        const option = {
          title: {
            top: 15,
            left: 'center',
            text: '成功失败对比图',
          },
          legend: {
            top: 120,
            left: 20,
            width: 100,
          },
          tooltip: {
            trigger: 'axis',
            showContent: true,
          },
          dataset: {
            source: [
              ['product', '2012', '2013', '2014', '2015', '2016', '2017'],
              ['Milk Tea', 5.5, 82.1, 88.7, 70.1, 53.4, 85.1],
              ['Matcha Latte', 51.1, 51.4, 55.1, 53.3, 73.8, 68.7],
              ['Cheese Cocoa', 40.1, 62.2, 69.5, 36.4, 45.2, 32.5],
              ['Walnut Brownie', 25.2, 37.1, 41.2, 18, 33.9, 49.1],
              ['axiba', 70.1, 3.4, 55.1, 53.3, 144.8, 68.7],
              ['juejuezi', 30.1, 62.2, 69.5, 3.4, 45.2, 32.5],
              ['yangleduo', 299.2, 44.1, 41.2, 18, 33.9, 49.1],
            ],
          },
          xAxis: { type: 'category' },
          yAxis: { gridIndex: 0 },
          grid: { top: '55%' },
          series: [
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'line',
              smooth: true,
              seriesLayoutBy: 'row',
              emphasis: { focus: 'series' },
            },
            {
              type: 'pie',
              id: 'pie',
              radius: '30%',
              center: ['50%', '25%'],
              emphasis: {
                focus: 'self',
              },
              label: {
                formatter: '{b}: {@2012} ({d}%)',
              },
              encode: {
                itemName: 'product',
                value: '2012',
                tooltip: '2012',
              },
            },
          ],
        };
        myChart.on('updateAxisPointer', (event) => {
          const xAxisInfo = event.axesInfo[0];
          if (xAxisInfo) {
            const dimension = xAxisInfo.value + 1;
            myChart.setOption({
              series: {
                id: 'pie',
                label: {
                  formatter: `{b}: {@[${dimension}]} ({d}%)`,
                },
                encode: {
                  value: dimension,
                  tooltip: dimension,
                },
              },
            });
          }
        });
        myChart.setOption(option);
      }
    },
  },
};
</script>

<style>
</style>



